<template>
  <div class="flight-card">
    <div class="row">
      <div class="col no-gutters aircraft-photo"><img src="@/assets/img/aircrafts/aircraft-1.jpg" alt="Aircraft"/></div>
      <div class="col no-gutters flight-body">
        <div class="row">
          <div class="row vertical-center title">
            <h6 class="m0">{{data.flight_code}}</h6><i class="separater fas fa-angle-right text-muted"></i><span class="aircraft-name text-muted">Bombardier CRJ200</span>
          </div>
          <div class="like-flight text-muted"><i class="fas fa-plus"></i></div>
        </div>
        <div class="row">
          <table>
            <tr>
              <td>Дата вылета:</td>
              <td class="date-1">{{data.from.date}}</td>
              <td>Время прилета:</td>
              <td class="time-1">{{data.from.time}}</td>
            </tr>
            <tr>
              <td>Время вылета:</td>
              <td class="time-2">{{data.to.date}}</td>
              <td>Время в пути:</td>
              <td class="time-3">{{data.to.time}}</td>
            </tr>
          </table>
        </div>
        <div class="row justify-content-between">
          <h5 class="m0 variety">Вероятость:<span class="data-variety">{{data.availability}}</span></h5>
          <h5 class="m0 price">Стоимость:<span class="data-price">{{data.cost}}<i class="fas fa-ruble-sign"></i></span></h5>
          <input class="small m0" type="button" value="Бронировать"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {





  /**
   * ==========
   * Параметры
   * ==========
   */
  props: ['data'],
}
</script>
