<template>
  <main class="site-content" id="js-ajax-content">
    <Search @search="findFlights"/>
    <section v-if="is_found" class="search-results">
      <!-- Результаты поиска -->
      <div v-if="flights" class="page-container">
        <h2 class="section-title h-decoration h-center"><span>Результаты поиска</span></h2>
        <Flight v-for="flight in flights"
          :key="flight.id"
          :data="flight"
        />
      </div>
      <!-- Если результатов нет -->
      <div v-else class="page-container">
        <h2 class="section-title h-decoration h-center"><span>По вашим требованиям рейсы отсутствуют</span></h2>
      </div>
    </section>
  </main>
</template>

<script>
import Search from '@/components/Index/search'
import Flight from '@/components/Search/flight'

export default {





  /**
   * ==========
   * Данные
   * ==========
   */
  data: () => ({
    is_found: false,
    flights: false,
  }),





  /**
   * ==========
   * Компоненты
   * ==========
   */
  components: {
    Search,
    Flight,
  },





  /**
   * ==========
   * Методы
   * ==========
   */
  methods: {
    async findFlights(query) {
      let response = await fetch (`http://ws-module-2.loc/api/flight${query}`)

      let data = (await response.json()).data

      let flights = [...data.flights_to, ...data.flights_back]

      this.is_found = true
      this.flights = flights.length ? flights : false
    },
  },
}
</script>
